import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth } from "../../actions";
import { StoreState } from "../../reducers";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/functions";

interface ShortlistingProps {
  auth: Auth;
}
interface ShortlistingState {
  loading: boolean;
  success: string;
  error: string;
}

export const RunShortlistAlgorithm = async (
  callBack: (loading: boolean, success: string, error: string, res: any) => void
) => {
  callBack(true, "", "", "");
  try {
    const res = await axios.post(`${API.APPLICATION}/settings`);
    callBack(false, "Done successfully!", "", res.data);
    console.log("Success: ", res);
  } catch (error: any) {
    callBack(false, "", errorToText(error), "");
    console.log("Error: ", { ...error });
  }
};

class _Shortlisting extends Component<ShortlistingProps, ShortlistingState> {
  constructor(props: ShortlistingProps) {
    super(props);

    this.state = {
      loading: false,
      success: "",
      error: "",
    };
  }

  render() {
    return <div>Shortlisting</div>;
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const Shortlisting = connect(mapStateToProps, {})(_Shortlisting);
